import { login } from '@/common/js/login.js';
import { sendAjax } from '@/common/js/sendAjax.js';
import config from '@/apiConfig';
const { dictDetailUrl,getNewsUrl} = config.api;
export default {
	data() {
		return {
			ylxxList: [],
			swiperList: [
				{
					id: 0,
					type: 'image',
					url: 'https://www.sxscott.com/img/banner.png'
				}
			],
			msgList: [],
			modalName: ''
		};
	},
	onLoad: function() {
		//判断是否第一次进入小程序		
		if(uni.getStorageSync('userInfo')){
			this.modalName =null
			this.getDict()
			this.getNews()
		}else{
			this.modalName = 'toUserLogin'
		}
	},
	methods: {
		//获取用户信息
		wxGetUserInfo(res) {
			var that = this;
			if (!res.detail.iv) {
				uni.showToast({
					title: '您取消了授权,登录失败',
					icon: 'none'
				});
				return false;
			}
			//授权成功回调
			let callback = {};
			callback.success = function() {
				that.modalName = null;
				that.getDict();
				that.getNews();
			};
			login(callback);
		},
		// 获取字典
		getDict() {
			var that = this;
			//娱乐休闲
			let infoOpt = {
				url: dictDetailUrl,
				type: 'POST',
				data: {
					type: 'YLXX',
					pageNum: 1,
					pageSize: 999
				}
			};
			let infoCb = {};
			infoCb.success = function(res) {
				that.ylxxList = res.list;
			};
			sendAjax(infoOpt, infoCb);
		},
		// 获取公告
		getNews() {
			var that = this;
			let infoOpt = {
				url: getNewsUrl,
				type: 'POST',
				data: {
					pageNum:1,
					pageSize:5
				}
			};
			let infoCb = {};
			infoCb.success = function(res) {
				that.msgList = res.list
			};
			sendAjax(infoOpt, infoCb);
		},
		wxGetPhoneNumber(res) {
			console.log(res);
		},
		toCleanNormal() {
			uni.navigateTo({
				url: '../cleanNormal/cleanNormal'
			});
		},
		gotoDetail(e) {
			if(e==1){
				uni.navigateTo({
					url: './lookImg'
				});
			}else{
				uni.showToast({
					title: '该功能暂未开放！',
					icon: 'none',
					duration: 1000
				});
			}
			
		}
	}
};
